@use 'base/variables' as v;

.formulario {
    .int-formulario{
        background-color: v.$azul;
        font-weight: 700;
        padding: 2rem;
        border: 3px solid darken(v.$azul, 5);
        border-radius: 2rem;
        input {
            margin-left: 1rem;
            margin-top: 0;
        }
    }
    label {
        margin: 1rem;
        display: block;
    }
    
}
.formulario-datos {

    .int-formulario{
        background-color: v.$azul;
        font-weight: 700;
        padding: 2rem;
        border: 3px solid darken(v.$azul, 5);
        border-radius: 2rem;
        display: flex;
        justify-content: space-between;
        .subir-logo {
            margin-right: 10%;
            margin-top: 2rem;
            margin-bottom: 2rem;
            input {
                margin-top: 2rem;
            }
            
        }
        .datos {
            display: flex;
            flex-direction: column;
            column-gap: 1rem;
        }
        
        input {
            margin-left: 1rem;
            margin-top: 0;
            width: 180%;
        }
        label {
            margin: 1rem;
        }
    }
    
}

.formulario-ticket {
    

    .int-formulario{
        background-color: v.$azul;
        font-weight: 700;
        padding: 2rem;
        border: 3px solid darken(v.$azul, 5);
        border-radius: 2rem;
        display: flex;
        justify-content: space-between;
        
        .datos {
            display: flex;
            flex-direction: column;
            column-gap: 1rem;
            width: 40rem;
            h2 {
                margin: 1rem 0;
                text-align: center;
            }
        }
        .ticket {
            border: 1px solid black;
            width: 50rem;
            margin-right: 10%;
            .fondo-ticket {
                background-color: v.$blanco;
                width: 40rem;
                height: 92rem;
                margin: 2rem auto;
            }
        }
        
        input {
            margin-left: 1rem;
            margin-top: 0;
        }
        label {
            margin: 1rem;
        }
        option {
            text-align: center;
        }
    }
    
}
.formulario-datos2 {

    .int-formulario2{
        min-width: 40rem;
        background-color: v.$azul;
        font-weight: 700;
        padding: 2rem;
        border: 3px solid darken(v.$azul, 5);
        border-radius: 2rem;
        display: flex;
        justify-content: space-between;
        
        .datos {
            display: flex;
            flex-direction: column;
            column-gap: 1rem;
        }
        
        input {
            margin-left: 1rem;
            margin-top: 0;
            width: 120%;
        }
        label {
            margin: 1rem;
        }
    }
    
}