@use 'base/variables' as v;

.columnas {
    display: grid;
    grid-template-columns: 1fr 3fr;
    
}
.categorias {
    thead {
        background-color: darken($color: v.$azul, $amount: 20);
        th{
            color: v.$blanco;
            text-transform: uppercase;
            padding: 2rem;
            width: 18rem;
        }
    }
    tbody{
        
        td{
            text-align: center;
            padding: 1rem;
            padding: 4rem 0;
            font-weight: bold;
            font-size: 2rem;
            background-color: lighten($color: v.$azul, $amount: 10);
            .categoria-enlace {
                padding: 3rem 1rem;
                color: v.$negro;
            }
            &.categoria-nombre {
                text-transform: uppercase;
            }
        }
        .opciones {
            background-color: v.$blanco;
            display: flex;
            flex-direction: column;
            align-items: initial;
            gap: 1rem;
        }
    }
    .td-imagen{
        .imagen-tabla{
            width: 12rem;
        }
    }
}
.articulos {
    width: 100%;
    thead {
        background-color: darken($color: v.$azul, $amount: 20);
        th{
            color: v.$blanco;
            text-transform: uppercase;
            padding: 2rem;
        }
    }
    tbody{
        
        td{
            padding: 1rem;
            text-align: center;
            font-weight: bold;
            font-size: 2rem;
            background-color: lighten($color: v.$azul, $amount: 10);
        }
        .opciones {
            background-color: v.$blanco;
            display: flex;
            flex-direction: column;
            align-items: initial;
            gap: 1rem;
        }
    }
    .td-imagen{
        .imagen-tabla{
            width: 12rem;
        }
    }
}
.editor-categorias {
    .int-formulario {
        display: flex;
        flex-direction: column;
        align-items: center;
        input{
            margin: 1rem 0;
        }
    }
}
.editor-articulos {
    .int-formulario {
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: center;
        input{
            margin: 1rem 0;
        }
    }
}
.contenido-articulos {
    justify-content: center;
}
.modal {
    .boton-rojo {
        margin: 0 0 1rem 1rem;
    }
    
}
