@use 'base/variables' as v;

.fechas{
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: v.$azul;
    align-items: center;
    gap: .5rem;
    margin-top: 5rem;
    padding: 1rem;
    border: 2px solid v.$azulOscuro;
    border-radius: 2rem;
    width: 20rem;
    label {
        font-weight: bold;
    }
    input {
        max-width: 14rem;
    }
}