@use 'variables' as v;
.seccion {
    margin-top: 2rem;
    margin-bottom: 2rem;
}
.texto-centrado {
    text-align: center;
}
.contenido-centrado {
    max-width: 80rem;
}
.alerta {
    padding: .5rem;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    margin: 1rem;
    color: v.$blanco;
    &.insertado {
        background-color: v.$verde;
    }
    &.error {
        background-color: v.$rojo;
    }
}
.separar-botones {
    display: flex;
    gap: 18rem;
}
.juntar-botones {
    display: flex;
    gap: 2rem;
}
.icono {
    width: 3rem;
}
.dividir-tablas{
    display: flex;
    justify-content: space-around;
}