@use 'variables' as v;

.boton-azul {
    margin-top: 2rem;
    margin-left: 4rem;
    padding: .5rem 1rem;
    font-weight: 700;
    background-color: v.$azul;
    border: 3px solid darken($color: v.$azul, $amount: 5);
    border-radius: 1rem;
    &:hover {
        background-color: v.$marine;
    }
}
.boton-volver {
    margin-top: 2rem;
    color: v.$negro;
    padding: .5rem 1rem;
    font-weight: 700;
    background-color: v.$azul;
    border: 3px solid darken($color: v.$azul, $amount: 5);
    border-radius: 1rem;
    margin-bottom: 2rem;
    display: block;
    width: 10rem;
    text-align: center;
    &:hover {
        background-color: v.$marine;
    }
}
.boton-derecha {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;
    
}
.guardar {
    width: 15rem;
    cursor: pointer;
    text-align: center;
    color: v.$negro;
}
.boton-rojo {
    padding: .5rem 1rem;
    display: block;
    font-weight: 700;
    background-color: lighten($color: v.$rojo, $amount: 50);
    border: 3px solid v.$rojo;
    border-radius: 1rem;
    cursor: pointer;
    &:hover {
        background-color: lighten($color: v.$rojo, $amount: 15);
    }
}
.boton-verde {
    padding: .5rem 1rem;
    font-weight: 700;
    background-color: lighten($color: v.$verde, $amount: 45);
    border: 3px solid v.$verde;
    border-radius: 1rem;
    cursor: pointer;
    &:hover {
        background-color: darken($color: v.$verde, $amount: 5);
    }
}
.boton-naranja {
    margin-top: 2rem;
    color: v.$negro;
    margin-left: 2rem;
    padding: .5rem 1rem;
    font-weight: 700;
    background-color: v.$naranja;
    border: 3px solid darken($color: v.$naranja, $amount: 5);
    border-radius: 1rem;
    margin-bottom: 2rem;
    cursor: pointer;
    display: block;
    width: 10rem;
    text-align: center;
    &:hover {
        background-color: v.$marron;
    }
}