@use 'variables' as v;

.ventana-modal {
    position: fixed;
    z-index: 0;
    background-color: v.$negroAlpha;
    top: 0;    
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
    .modal {
        position: relative;
        left: 30%;
        top: 15%;
        width: 70rem;
        height: 75rem;
        background-color: v.$blanco;
        padding: 2rem;
        border: 1rem solid v.$azul;
        border-radius: 1rem;
        z-index: 1;
        overflow: auto;
        .contenido-modal {
            display: flex;
            gap: 2rem;
            
        }
        .contenido-modal2 {
            display: flex;
            justify-content: center;
            gap: 2rem;
        }
    }
    .modal2 {
        position: relative;
        left: 30%;
        top: 25%;
        width: 70rem;
        height: 25rem;
        background-color: v.$blanco;
        padding: 2rem;
        border: 1rem solid v.$azul;
        border-radius: 1rem;
        z-index: 1;
        overflow: auto;
        .contenido-modal2 {
            display: flex;
            justify-content: center;
            gap: 2rem;
        }
    }
    .modal3 {
        position: relative;
        left: 25%;
        top: 15%;
        width: 100rem;
        height: 75rem;
        background-color: v.$blanco;
        padding: 2rem;
        border: 1rem solid v.$azul;
        border-radius: 1rem;
        z-index: 1;
        overflow: auto;
        .contenido-modal3 {
            display: flex;
            justify-content: center;
            gap: 2rem;
        }
    }
    
}
.modal4 {
    position: relative;
    left: 30%;
