@use 'base/variables' as v;
@use 'base/mixins' as m;

.body-color{
    background-color: rgba(162, 233, 246, 0.115);
}

.dividir {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
}
.ventana-cobro{
    @include m.grid(2, 1rem);
    .div-articulos {
        display: flex;
        flex-direction: column;
        min-width: 62rem;
        max-height: 45rem;
        overflow: auto;
        justify-content: space-between;
        border: 1px solid v.$azulOscuro;
        .cambiar-precio {
            color: v.$negro;
            border: none;
            background-color: transparent;
            cursor: pointer;
        }
        .totales {
            font-weight: 500;
            font-size: 3rem;
            margin-left: 1rem;
            background-color: transparent;
            border: none;
            width: 10rem;

            &:focus {
                outline: none;
            }
            
        }
    }
    .form-totales {
        label {
            margin-left: 1rem;
            font-weight: 700;
            font-size: 2.5rem;
        }
        
    }
    .teclado {
        display: flex;
        flex-direction: column;
        width: 16rem;
        gap: 1rem;
        height: 150%;
        .buscador-form {
            display: flex;
            flex-direction: column;
            input {
                width: 16rem;
            }
            .buscador {
                margin-bottom: 1rem;
            }
        }
        .teclas {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: .5rem;
            margin-top: 1rem;
            .numerico, .numerico-borrar, .numerico-intro {
                background-color: lighten($color: v.$azulOscuro, $amount: 40);
                border: 2px solid v.$azulOscuro;
                border-radius: 1rem;
                cursor: pointer;
                height: 5rem;
                &:nth-last-child(1) {
                    grid-column-end: span 2;
                }
            }
        }
        
    }
}

.categorias-articulos {
    display: flex;
    flex-direction: column;
    gap: 3rem;
}
.mostrarCategorias {
    margin-top: 8rem;
    overflow-y: auto;
    gap: 1rem;
    border: 1px solid v.$azulOscuro;
    padding: .5rem;
    height: 21rem;
    max-width: 70rem;
    .boton-seleccion {
        border: 2px solid darken($color: v.$azul, $amount: 40);
        background-color: v.$azul;
        font-weight: bold;
        text-transform: uppercase;
        border-radius: 1rem;
        margin: 1rem;
        height: 8rem;
        width: 14rem;
        cursor: pointer;
    }
}
.mostrarArticulos {
    overflow-y: auto;
    gap: 1rem;
    border: 1px solid v.$azulOscuro;
    padding: .5rem;
    height: 41rem;
    max-width: 70rem;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(10, 1fr);

    .boton-seleccion {
        border: 2px solid darken($color: v.$azul, $amount: 40);
        background-color: v.$azul;
        font-weight: bold;
        text-transform: uppercase;
        border-radius: 1rem;
        margin: 1rem;
        height: 8rem;
        width: 14rem;
        cursor: pointer;
    }
}
.ventana-menu {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    .menu-cobrar {
        background-color: lighten($color: v.$azulOscuro, $amount: 40);
        border: 2px solid v.$azulOscuro;
        border-radius: 1rem;
        cursor: pointer;
        font-weight: bold;
        text-transform: uppercase;
        margin: 1rem;
        height: 8rem;
        &:nth-of-type(1) {
            background-color: v.$gris;
        }
        &:nth-of-type(4){
            background-color: lighten($color: v.$rojo, $amount: 20);
        }
        &:nth-of-type(5){
            background-color: lighten($color: v.$naranja, $amount: 10);
            grid-column-end: span 2;
        }
    }
    
}

.boton-total {
    display: none;
}


.formulario-totales {
    background-color: v.$azul;
    font-weight: 700;
    font-size: 2.5rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
    padding: 2rem;
    border-radius: 2rem;
    border: 2px solid darken($color: v.$azul, $amount: 50);
}
.ventana-menu-2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    .menu-cobrar {
        background-color: lighten($color: v.$naranja, $amount: 20);
        border: 2px solid v.$azulOscuro;
        border-radius: 1rem;
        cursor: pointer;
        font-weight: bold;
        text-transform: uppercase;
        margin: 1rem;
        height: 8rem;
        &:nth-of-type(1) {
            background-color: v.$marine;
        }
        &:nth-of-type(3) {
            background-color: lighten($color: v.$azulOscuro, $amount: 40);
        }
    }
}
.ultimo-ticket {
    background-color: v.$marine;
    border: 2px solid v.$azulOscuro;
    border-radius: 1rem;
    margin-top: 3rem;
    padding: 1rem;
    height: 8rem;
    cursor: pointer;
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
    
    &:nth-last-child(1) {
        background-color: lighten($color: v.$naranja, $amount: 20);
        grid-column-end: span 3;
    }
}
.contenedor-azul {
    background-color: v.$azul;
    border: 2px solid v.$azulOscuro;
    border-radius: 2rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 1rem;
    font-weight: bold;
    font-size: 2rem;
    
    label {
        margin: 1rem;
    }
    input {
        margin: 1rem;
    }
}