@use 'base/variables' as v;

.usuarios {
    width: 100%;
    thead {
        background-color: darken($color: v.$azul, $amount: 20);
        th{
            color: v.$blanco;
            text-transform: uppercase;
            padding: 2rem;
        }
    }
    tbody{
        
        td{
            padding: 1rem;
            text-align: center;
            font-weight: bold;
            font-size: 2rem;
            background-color: lighten($color: v.$azul, $amount: 10);
        }
        .opciones {
            background-color: v.$blanco;
            display: flex;
            flex-direction: column;
            align-items: initial;
            gap: 1rem;
        }
    }
    .td-imagen{
        .imagen-tabla{
            width: 12rem;
        }
    }
}