@use 'base/variables' as v;
@use 'base/mixins' as m;


.menu {
    display: flex;
    justify-content: space-between;
}
.menu-principal {
    .iconos-principal {
        &:nth-child(3) {
            background-color: darken($color: v.$azul, $amount: 20)
        }
        &:nth-child(4) {
            background-color: darken($color: v.$gris, $amount: 40)
        }
        background-color: darken($color: v.$gris, $amount: 15);
        border-radius: 2rem;
        padding: 1rem 0;
        margin-bottom: 2rem;
        cursor: pointer;
        display: flex;
        flex-direction: column;
        width: 25rem;
        text-align: center;
        align-items: center;
        color: v.$blanco;
        font-size: 2.2rem;
        text-transform: uppercase;
        font-weight: bold;
        img{
            max-width: 15rem;
        }
        
    }
    .iconos-principal-venta{
        cursor: pointer;
        background-color: v.$rojo;
        border-radius: 2rem;
        padding: 1rem 0;
        margin-bottom: 2rem;
        display: flex;
        flex-direction: column;
        width: 25rem;
        text-align: center;
        align-items: center;
        color: v.$blanco;
        font-size: 2.2rem;
        text-transform: uppercase;
        font-weight: bold;
        img{
            max-width: 15rem;
        }
    }
}
.menu-dividido{
    .menu-ajustes{
        @include m.grid (3, 1rem);
        margin-bottom: 5rem
    }
    .menu-opciones {
        @include m.grid (3, 1rem);
    }
    .iconos-ajustes{
        background-color: lighten($color: v.$azulOscuro, $amount: 10);
        border-radius: 2rem;
        padding: 1rem 0;
        display: flex;
        flex-direction: column;
        width: 25rem;
        text-align: center;
        align-items: center;
        color: v.$blanco;
        font-size: 2.2rem;
        text-transform: uppercase;
        font-weight: bold;
        img{
            max-width: 15rem;
        }
    }
    .iconos-opciones {
        
        background-color: darken($color: v.$azul, $amount: 20);
        border-radius: 2rem;
        padding: 1rem 0;
        display: flex;
        flex-direction: column;
        width: 25rem;
        text-align: center;
        align-items: center;
        color: v.$blanco;
        font-size: 2.2rem;
        text-transform: uppercase;
        font-weight: bold;
        img{
            max-width: 15rem;
        }
    }
}
    
.abrir-caja {
    background-color: lighten($color: v.$naranja, $amount: 20);
    border: 2px solid v.$azulOscuro;
    border-radius: 1rem;
    width: 100%;
    cursor: pointer;
    font-weight: bold;
    text-transform: uppercase;
    margin: 1rem 1rem 1rem 0;
    height: 8rem;
}