

html {
    font-size: 62.5%;
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    font-size: 1.6rem; // para 10px = 1 rem
}
@media (max-width: 1840px) {
    body {
        zoom: 80%;
      }
}

.contenedor {
    width: 95%;  // margenes mejor vista
    max-width: 85%; // vista en pantallas grandes hasta un tope
    margin: 0 auto; // centrar contenido
}

a {
    text-decoration: none;
}

img,
picture {
    width: 100%;
}

h1 {
    font-size: 3.8rem;
}
h2 {
    font-size: 3.4rem;
}
h3 {
    font-size: 3rem;
}
h4 {
    font-size: 2.6rem;
}