@use 'base/variables' as v;

.contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-top: 2rem;
    
    
    .logo-menu {
        max-width: 15rem;
    }
    .img-usuario {
        width: 100%;
        max-width: 10rem;
        border-radius: 5rem;
    }
}
.usuario {
    display: flex;
    align-items: flex-end;
    gap: 2rem;
    .user {
        color: v.$negro;
        font-weight: bold;
        font-size: 2rem;
        padding: 1rem;
        border: 2px solid v.$azul;
        border-radius: 2rem;
        background-color: rgba(162, 233, 246, 0.115);;
    }
    .fecha {
        font-weight: bold;
        width: 8rem;
        margin-left: 1rem;
    }
}
